.App {
  text-align: center;
}

.App-logo {
  animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
.card {
  width: 250px;
  height: 250px;
  margin: 15px
}
.media {
  height: 150px;
}
.root {
  flex-grow: 1;
}
.grow {
  flex-grow: 1;
}
.menuButton {
  margin-left: -12;
  margin-right: 20;
}
.routesList {
}
.routeCard {
  margin: 10px
}
.route-card{
  width: 350px;
  height: 350px;
  display: block;
  float: left;
  position: relative;
  margin: 20px;
  padding: 20px;
  border-width: 1px;
  border-style: solid;
  border-color: #007a9c;
  border-radius: 20px;
  overflow: hidden;
  box-shadow: 0 4px 8px 0 rgba(0,0,0,0.2);
  transition: 0.3s;
}

.route-card:hover {
  box-shadow: 0 8px 16px 0 rgba(0,0,0,0.2);
}
.route-card-img{
  height: 160px;
}
.route-card-title{
  font-size: 20px;
  font-weight: 800;
  color: #007a9c;
  text-align: left;
  margin-bottom: 10px;
}
.route-card-description,
.route-card-level{
  font-size: 14px;
  font-weight:600;
  color: #007a9c;
  text-align: left;
  margin-bottom: 5px;
}

.route-container{
  display: grid;
  grid-column-gap: 20px;
  grid-row-gap: 20px;
  grid-template-columns: 40vh auto;
  margin: 10px;
}

.route-holds-list{
  grid-column: 1;
}
.route-full-img{
  grid-column: 2;
  height: 90vh;
  background-size: cover;
  background-repeat: no-repeat;
  overflow: hidden;
  position: relative;
}

.route-hold-marker{
  background-color: transparent;
  border-style: dashed;
  border-color: blueviolet;
  height: 50px;
  width: 50px;
  border-radius: 25px;
}

.route-hold-marker:hover{
  background-color: rgba(255, 255, 255, 0.349);
}